html{
  box-sizing: border-box;
}

*,
*::before,
*::after{
  box-sizing: inherit;
}

body{
  padding: 0;
  margin: 0;
}


.side-bar{
  z-index: 2;
  position: fixed;
  background-color: blue;
  height: 100%;
  width: 200px;
}

.side-bar-button{
  height: 50px;
  width: 50px;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  
}

.side-bar li{
  display: flex;
  align-items: center;
}